  padding-bottom: 23px;
}
.parameter__label {
  margin-left: 20px;
}
.parameter__span {
  font-family: "Oswald", "Tahoma", sans-serif;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  font-size: 16px;
  text-transform: uppercase;
  color: #222222;
  display: inline-block;
  vertical-align: middle;
}
.parameter__radio {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.parameter__radio-image {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
}
.parameter__radio-image::before {
  content: "";
  position: absolute;
  display: block;
  transition: 0.2s;
  width: 12px;
  height: 12px;
  background-color: #68b738;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  opacity: 0;
}
.parameter__radio:checked + .parameter__radio-image::before {
  opacity: 1;
}
.parameter__radio:disabled + .parameter__radio-image::before {
  opacity: 0.3;
}
.parameter__radio:focus + .parameter__radio-image {
  box-shadow: 0 0 2px 2px #4d90f0;
}
.parameter__radio-image:hover {
  border-color: #bababa;
}
.parameter__label:first-child {
  margin-top: 19px;
}
.parameter__label:not(:first-child) {
  margin-top: 13px;
}
.parameter__label:active {
  opacity: 0.3;
}
